@media screen and (max-width: 743px) {
  .burger__btn {
    width: 34px;
    height: 21px;
    display: block;
    position: relative;
  }

  .burger__btn:before, .burger__btn:after {
    content: "";
    width: 100%;
    height: 3px;
    background-color: var(--icon-menu-cl);
    transition-property: transform;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);
    position: absolute;
    left: 0;
  }

  .burger__btn:before {
    top: 0;
  }

  .burger__btn:after {
    bottom: 0;
  }
}

.burger__btn.active:before {
  top: 7px;
  transform: rotate(45deg);
}

.burger__btn.active:after {
  top: 7px;
  transform: rotate(-45deg);
}

.burger__btn.active .burger__icon {
  opacity: 0;
}

.burger__icon {
  width: 100%;
  height: 3px;
  background-color: var(--icon-menu-cl);
  position: absolute;
  top: 9px;
  left: 0;
}

@media screen and (max-width: 743px) {
  .mobile-menu {
    width: 100vw;
    height: 100vh;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition-property: transform, opacity, visibility;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);
    background-color: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    overflow-y: scroll;
    transform: translateY(-100%);
  }
}

@media screen and (min-width: 744px) {
  .mobile-menu {
    flex-grow: 1;
